import React from 'react';
import video from './video.mp4';

export const Video = ({ content }) => (
  <div className='bg-grey modal'>
    <video className='video' autoPlay='true' muted='true' controls='false'>
      <source src={video} type='video/mp4' />
    </video>
  </div>
);



