import React from 'react';
import fish from './fish.png';
import zipline from './zipline.png';
import camp from './camp.png';
import cabin from './cabin.png';
import eat from './eat.png';
import paint from './paint.png';

export const Places = () => (
  <div className='trails modal bg-white'>
    <div className='padded light-font'>
   Click to exlore, download and discover places and services offered in the park
      <div className='activity-icon'>
        <img className='icon-md' src={camp} alt={'Tribu Logo'} />
        <img className='icon-md' src={cabin} alt={'Tribu Logo'} />
        <img className='icon-md' src={eat} alt={'Tribu Logo'} />
        <img className='icon-md' src={fish} alt={'Tribu Logo'} />
        <img className='icon-md' src={zipline} alt={'Tribu Logo'} />
        <img className='icon-md' src={paint} alt={'Tribu Logo'} />
      </div>

    </div>
  </div>
);



